"use client";

import { useEffect, useState } from "react";
import { usePage } from "@/hooks/usePage";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { User, Mail, Shield } from "lucide-react";

// Tokens used: bg-surface, text-primary, text-muted, border, surfaceElevated

interface LecturerProfile {
  name: string;
  email: string;
  department: string;
  staff_id: string;
}

export default function LecturerProfilePage() {
  const { setPage } = usePage();
  const [profile, setProfile] = useState<LecturerProfile>({
    name: "",
    email: "",
    department: "",
    staff_id: "",
  });
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    setPage("Profile");
    // TODO: Replace with real API call
    setTimeout(() => {
      setProfile({
        name: "Dr. Aloye Breakthrough",
        email: "breakthrough@afued.edu.ng",
        department: "Computer Science",
        staff_id: "AFUED/CS/LEC/015",
      });
      setLoading(false);
    }, 500);
  }, [setPage]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setProfile({ ...profile, [e.target.name]: e.target.value });
  };

  const handleSave = () => {
    setSaving(true);
    setTimeout(() => {
      setSaving(false);
      alert("âœ… Profile updated successfully!");
    }, 1000);
  };

  if (loading)
    return (
      <div className="max-w-6xl mx-auto p-6 text-center text-muted">
        Loading profile...
      </div>
    );

  return (
    <div className="max-w-6xl mx-auto p-6">
      <Card className="rounded-xl shadow-lg bg-surface max-w-md mx-auto">
        <CardContent className="p-6">
          <div className="flex items-center gap-3 mb-6">
            <User className="w-6 h-6 text-primary" />
            <h1 className="text-2xl font-semibold text-primary">My Profile</h1>
          </div>

          <div className="space-y-4">
            <div>
              <Label className="text-muted">Full Name</Label>
              <Input
                name="name"
                value={profile.name}
                onChange={handleChange}
                className="mt-1"
              />
            </div>

            <div>
              <Label className="text-muted">Email Address</Label>
              <div className="flex items-center gap-2 mt-1">
                <Mail className="w-4 h-4 text-muted" />
                <Input
                  name="email"
                  value={profile.email}
                  onChange={handleChange}
                  type="email"
                />
              </div>
            </div>

            <div>
              <Label className="text-muted">Department</Label>
              <Input
                name="department"
                value={profile.department}
                onChange={handleChange}
                className="mt-1"
              />
            </div>

            <div>
              <Label className="text-muted">Staff ID</Label>
              <Input
                name="staff_id"
                value={profile.staff_id}
                onChange={handleChange}
                disabled
                className="mt-1 opacity-70"
              />
            </div>
          </div>

          <div className="mt-6 flex justify-end">
            <Button onClick={handleSave} disabled={saving}>
              {saving ? "Saving..." : "Save Changes"}
            </Button>
          </div>

          {/* Change Password Section */}
          <div className="mt-10 border-t border-border pt-6">
            <div className="flex items-center gap-2 mb-4">
              <Shield className="w-5 h-5 text-primary" />
              <h2 className="text-lg font-medium text-primary">
                Change Password
              </h2>
            </div>
            <div className="space-y-3">
              <Input type="password" placeholder="Current Password" />
              <Input type="password" placeholder="New Password" />
              <Input type="password" placeholder="Confirm New Password" />
            </div>
            <div className="mt-4 flex justify-end">
              <Button variant="outline">Update Password</Button>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
